import ClientOAuth2 from "client-oauth2";
import qs from "qs";

import chrome from '../../lib/jsl/chrome/chrome';
import Window from '../../lib/jsl/chrome/window';

interface IProps {
  oauth2ClientId: string;
  accessTokenUri: string;
  authorizationUri: string;
  scopes?: string[];
  asyncui?: any;
}

class AuthService {
  dialog: any = null;

  auth: any = null;

  checkInterval: number = null;

  asyncui: any = null;

  constructor(opts: IProps ) {

    this.asyncui = opts.asyncui;

    const redirectUri = /esna\.com$/.test(location.hostname)
      ? 'https://manage1.esna.com/equinox.mso/token.redirect'
      : /avaya\.com$/.test(location.hostname)
        ? 'https://sso.apps.avaya.com/clients/scheduler/token.redirect'
        : null;

    this.auth = new ClientOAuth2({
      clientId        : opts.oauth2ClientId,
      accessTokenUri  : opts.accessTokenUri,
      authorizationUri: opts.authorizationUri,
      redirectUri     : redirectUri,
      scopes          : opts.scopes,
      state           : this.generateState()
    });
  }

  open = async () => {
    const apppath = location.protocol + '//' + location.hostname + location.pathname;
    let uri = this.auth.code.getUri();
    if (
      (!this.asyncui && !(chrome && chrome.runtime)) ||
      this.asyncui && /^(Outlook|OutlookWebApp)$/.test(this.asyncui.hostName)
    ) {
      uri = apppath.replace(/(.*)\/.*\.html$/i, '$1/redirect.html') + '?' + uri;
    }

    try {
      if (this.asyncui) {
        const result = await this.asyncui.open(uri);
        this.dialog = result.value;
        return this.dialogCallback(result);
      } else {
        this.dialog = Window.open(uri, 'oauth');
        if (!this.dialog) {
          throw new Error('Window blocked');
        }
        return new Promise((resolve) => {
          this.startCheck(() => resolve(this.onClose()));
        });
      }
    } catch (err) {
      if (err.error) {
        return this.processEvent(err);
      }
      throw err;
    }
  }

  dialogCallback(asyncResult) {
    if (asyncResult.error) {
      throw new Error(asyncResult.error.message);
    }
    return new Promise((resolve, reject) => {
      this.startCheck(() => resolve(this.onClose()));
      this.dialog.addEventHandler(Office.EventType.DialogEventReceived, (evt) => {
        try {
          resolve(this.processEvent(evt));
        } catch(e) {
          reject(e);
        }
      });
    });
  }

  startCheck(callback ?: () => void) {
    this.checkInterval = setInterval(() => {
      const state = localStorage.getItem(this.auth.options.state);
      if (state) {
        this.dialog && this.dialog.close();
      }
      if (state || this.dialog && this.dialog.closed) {
        callback && callback();
      }
    }, 500);
  }

  async onClose() : Promise<string> {
    const hash = localStorage.getItem(this.auth.options.state);
    clearInterval(this.checkInterval);
    if (hash) {
      const params = qs.parse(hash.slice(1))
      const tokenResp = await this.auth.code.getToken(
        `${ this.auth.options.redirectUri }?${qs.stringify({ state: this.auth.options.state, code: params.code })}`
      );
      return tokenResp.accessToken;
    } else {
      throw new Error('no hash');
    }
  }

  processEvent(evt) {
    let error;
    switch (evt.error) {
      case 12002:
        error = 'The dialog box has been directed to a page that it cannot find or load, or the URL syntax is invalid.';
        break;
      case 12003:
        error = 'The dialog box has been directed to a URL with the HTTP protocol. HTTPS is required.';
        break;
      case 12006:
        return this.onClose();
      default:
        error = evt.error;
        break;
    }
    throw error;
  }

  generateState() : string {
    const page = location.protocol + '//' + location.hostname + location.pathname;
    const id = page.replace(/\/[^\/]*\.html/i, '/auth.html');
    return `${ uuidv4() }---${id}---`;
  }
}
// See https://stackoverflow.com/a/2117523
function uuidv4() : string {
  return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
    var r = Math.random() * 16 | 0, v = c === 'x' ? r : (r & 0x3 | 0x8);
    return v.toString(16).toUpperCase();
  });
}

export default AuthService;


