define(function(require, exports, module) {
  'use strict';

  var channel = require('/lib/jsl/chrome/rpc/server');

  function Channel() {
    this.onConnect = channel.onConnect;
    this.onDisconnect = channel.onDisconnect;
  }

  module.exports = new Channel();
});


