define(function (require, exports, module) {
  'use strict';

  var API = require('../api/api');

  var methods = {
    info: {
      uri: '/ups/resources/$(tenantId)',
      method: 'GET',
      headers: {
        'authorization': '$(token)'
      },
      body: null,
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 's', format: 'UPToken $0' }
      },
      response: {
        portalVersion: { key: 'portalVersion', flags: 'Sr' },
        multitenant: { key: 'multitenant', flags: 'br' },
        iwaEnabled: { key: 'iwaEnabled', flags: 'br' },
        scopiaUrl: { key: 'scopiaDesktopServerURL', flags: 's' },
        oauth2AuthenticationUrl: { key: 'oauth2AuthenticationUrl', flags: 's' },
        supportedAuthType: { key: 'supportedAuthType', flags: 's' },
        oauthCodeToTokenExchangeUrl: { key: 'oauthCodeToTokenExchangeUrl', flags: 's' },
        oauth2ClientId: { key: 'oauth2ClientId', flags: 's' },
        self: {
          $v: {
            email: { key: 'self.email', flags: 'sr' },
            firstName: { key: 'self.firstName', flags: 's' },
            lastName: { key: 'self.lastName', flags: 's' },
            userId: { key: 'self.userId', flags: 's' },
            scopiaUserId: { key: 'self.scopiaUserId', flags: 's' },
            scopiaTenantId: { key: 'self.scopiaMemberId', flags: 's' }
          }
        },
        loginInfo: {
          $v: {
            href: { key: 'resources.authentication.POST.login.href', flags: 'Sr' },
            requestTypes: { key: 'resources.authentication.POST.login.requestTypes', flags: 'O' },
            responseTypes: { key: 'resources.authentication.POST.login.responseTypes', flags: 'O' },
          }
        },
        getUserDetailsInfo: {
          $v: {
            href: { key: 'resources.userdetails.GET.getUserDetails.href', flags: 'Sr' },
            requestTypes: { key: 'resources.userdetails.GET.getUserDetails.requestTypes', flags: 'O' },
            responseTypes: { key: 'resources.userdetails.GET.getUserDetails.responseTypes', flags: 'O' },
          }
        }
      },
    },
    refreshToken: {
      uri: '/ups/resources/$(tenantId)authentication/login',
      method: 'POST',
      headers: {
        'content-type': 'application/json',
        'authorization': '$(token)'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'Sr', format: 'UPToken $0' },
        userId: { key: '$(userId)', flags: 's' },
        passwordHash: { key: '$(passwordHash)', flags: 's' },
        aliasId: { key: '$(aliasId)', flags: 's', default: 'default' }
      },
      body: {
        'login': '$(userId)',
        'encryptedPassword': '$(passwordHash)',
        'organizationAlias': '$(aliasId)'
      },
      response: {
        token: { key: 'token', flags: 'Sr' },
        expires: { key: 'expirationTime', flags: 'D' },
        passwordHash: { key: 'encryptedPassword', flags: 's' }
      }
    },
    login: {
      uri: '/ups/resources/$(tenantId)authentication/login',
      method: 'POST',
      headers: {
        'content-type': 'application/json'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        aliasId: { key: '$(aliasId)', flags: 's', default: 'default' },
        userId: { key: '$(userId)', flags: 's' },
        password: { key: '$(password)', flags: 's' }
      },
      body: {
        'login': '$(userId)',
        'organizationAlias': '$(aliasId)',
        'password': '$(password)'
      },
      response: {
        token: { key: 'token', flags: 'Sr' },
        expires: { key: 'expirationTime', flags: 'D' },
        passwordHash: { key: 'encryptedPassword', flags: 's' }
      }
    },
    loginV2: {
      uri: '/ups/resources/$(tenantId)authentication/login',
      method: 'POST',
      headers: {
        'content-type': 'application/vnd.avaya.portal.authentication.login.v2+json',
        'authorization': '$(credentials)'
      }, 
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        aliasId: { key: '$(aliasId)', flags: 's', default: 'default' },
        credentials: { key: '$(credentials)', flags: 'Sr', format: 'UnifiedPortal $0' },
      },
      body: {
        'organizationAlias': '$(aliasId)',
      },
      response: {
        token: { key: 'token', flags: 'Sr' },
        expires: { key: 'expirationTime', flags: 'D' },
        passwordHash: { key: 'encryptedPassword', flags: 's' }
      }
    },
    loginIWA: {
      uri: '/ups/resources/$(tenantId)authentication/login?useSSO=true',
      method: 'POST',
      credentials: true,
      headers: {
        'content-type': 'application/json'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        aliasId: { key: '$(aliasId)', flags: 's', default: 'default' },
      },
      body: {
        'organizationAlias': '$(aliasId)',
      },
      response: {
        token: { key: 'token', flags: 'Sr' },
        expires: { key: 'expirationTime', flags: 'D' },
        passwordHash: { key: 'encryptedPassword', flags: 's' }
      }
    },
    getRooms: {
      uri: '/ups/resources/$(tenantId)conference/virtual_room',
      method: 'POST',
      headers: {
        'content-type': 'application/vnd.avaya.portal.conference.virtual_room.v1+json',
        'authorization': '$(token)'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        aliasId: { key: '$(aliasId)', flags: 's', default: 'default' },
        token: { key: '$(token)', flags: 'Sr', format: 'UPToken $0' },
        offset: { key: '$(offset)', flags: 'i', default: 1 },
        pageSize: { key: '$(pageSize)', flags: 'i', default: 20 },
        userId: { key: '$(userId)', flags: 'Sr' }
      },
      body: {
        memberId: '$(aliasId)',
        offset: '$(offset)',
        pageSize: '$(pageSize)',
        loginId: '$(userId)'
      },
      response: {
        totalCount: { key: 'totalCount ', flags: 'i' },
        rooms: [{
          id: { key: 'virtualRoom.#.virtualRoomId', flags: 'Sr' },
          name: { key: 'virtualRoom.#.name', flags: 'Sr' },
          number: { key: 'virtualRoom.#.number', flags: 'Sr' },
          userId: { key: 'virtualRoom.#.userId', flags: 'Sr' },
          accessPIN: { key: 'virtualRoom.#.accessPIN', flags: 'B', default: '' },
          
          autoExtend: { key: 'virtualRoom.#.autoExtend', flags: 'br' },
          blockDialIn: { key: 'virtualRoom.#.blockDialIn', flags: 'br' },
          isDefault: { key: 'virtualRoom.#.default', flags: 'br' },
          maxParticipants: { key: 'virtualRoom.#.maxParticipants', flags: 'ir' },
          moderatorPIN: { key: 'virtualRoom.#.moderatorPIN', flags: 'B' },
          oneTimePINRequired: { key: 'virtualRoom.#.oneTimePINRequired', flags: 'b' },
          priority: { key: 'virtualRoom.#.priority', flags: 'er' },
          isPublic: { key: 'virtualRoom.#.public', flags: 'br' },
          servicePrefix: { key: 'virtualRoom.#.servicePrefix', flags: 'sr' },
          serviceTemplateId: { key: 'virtualRoom.#.serviceTemplateId', flags: 'sr' },
          streamingStatus: { key: 'virtualRoom.#.streamingStatus', flags: 'er' },
          waitingRoom: { key: 'virtualRoom.#.waitingRoom', flags: 'br' },
          
          allowKnocking: { key: 'virtualRoom.#.allowKnocking', flags: 'br' },
          allowRecording: { key: 'virtualRoom.#.allowRecording', flags: 'er' },
          allowStreaming: { key: 'virtualRoom.#.allowStreaming', flags: 'er' },

          durationAfterLeft: { key: 'virtualRoom.#.advancedProperties.durationAfterLeft', flags: 'T' },
          minutesBeforeTermination: { key: 'virtualRoom.#.advancedProperties.minutesBeforeTermination', flags: 'T' },
          terminationCondition: { key: 'virtualRoom.#.advancedProperties.terminationCondition', flags: 'e' },

          portsFullHD: { key: 'virtualRoom.#.reservedPorts.fullHD', flags: 'i', default: 0 },
          portsHD: { key: 'virtualRoom.#.reservedPorts.hd', flags: 'i', default: 0 },
          portsRegular: { key: 'virtualRoom.#.reservedPorts.regular', flags: 'i', default: 0 },
          portsSD: { key: 'virtualRoom.#.reservedPorts.sd', flags: 'i', default: 0 }
        }]
      }
    },
    getDialInfo: {
      uri: '/ups/resources/$(tenantId)conference/dialing_info',
      method: 'POST',
      headers: {
        'authorization': '$(token)',
        'content-type': 'application/vnd.avaya.portal.conference.dialing_info.v1+json'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },

        accessPIN: { key: '$(accessPIN)', flags: 's' },
        confGID: { key: '$(confGID)', flags: 's' },
        dialableNumber: { key: '$(dialableNumber)', flags: 'Sr' },
        forReservation: { key: '$(forReservation)', flags: 'b' },
        loginId: { key: '$(loginId)', flags: 's' },
        memberId: { key: '$(memberId)', flags: 's' },
        servicePrefix: { key: '$(servicePrefix)', flags: 's' },
        serviceTemplateId: { key: '$(serviceTemplateId)', flags: 's' },
        userId: { key: '$(userId)', flags: 's' },
      },
      body: {
        'AccessPIN': '$(accessPIN)',
        'ConfGID': '$(confGID)',
        'DialableNumber': '$(dialableNumber)',
        'ForReservation': '$(forReservation)',
        'LoginId': '$(loginId)',
        'MemberId': '$(memberId)',
        'ServicePrefix': '$(servicePrefix)',
        'ServiceTemplateId': '$(serviceTemplateId)',
        'UserId': '$(userId)',
      },
      response: {
        dialingInfoText: { key: 'DialingInfo.Description', flags: 's' },
        dialingInfoHTML: { key: 'DialingInfo.DescriptionOfHTML', flags: 's' },
        location: { key: 'DialingInfo.LocationInfo', flags: 's' },
        childMeetingNumber: { key: 'DialingInfo.ChildMeetingNumber', flags: 's' },
        meetingPin: { key: 'DialingInfo.MeetingPin', flags: 's' },
        requestId: { key: 'RequestID', flags: 's' },
        result: { key: 'ReturnValue', flags: 'er' }
      }
    },
    getUserDetails: {
      uri: '$(uri)',
      method: 'GET',
      headers: {
        'authorization': '$(token)',
        'content-type': '$(contentType)'
      },
      args: {
        uri: { key: '$(uri)', flags: 'S' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        contentType: { key: '$(contentType)', flags: 'S' },
      },
      body: null,
      response: {
        canReserveMeetingsFromOutlookPlugin: { key: 'conferencing.canReserveMeetingsFromOutlookPlugin', flags: 'b' },
        virtualRoomSettings: [{
          number: { key: 'conferencing.virtualRoomSettings.#.number', flags: 's' },
          servicePrefix: { key: 'conferencing.virtualRoomSettings.#.servicePrefix', flags: 's' },
          serviceTemplateId: { key: 'conferencing.virtualRoomSettings.#.serviceTemplateId', flags: 's' },
          protectMeetingWithParticipantId: { key: 'conferencing.virtualRoomSettings.#.protectMeetingWithParticipantId', flags: 'b' }
        }]
      }
    },
    search: {
      uri: '/ups/resources/$(tenantId)conference/?conferenceId=$(conferenceId)',
      method: 'GET',
      headers: {
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' },
      },
      response: {
        conferences: { key: 'conferences', flags: '' }
      },
    },
    searchByStartTime: {
      uri: '/ups/resources/$(tenantId)conference/?startTime=$(startTime)',
      method: 'GET',
      headers: {
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        startTime: { key: '$(startTime)', flags: 's' },
      },
      response: {
        conferences: { key: 'conferences', flags: '' }
      },
    },
    searchByGlobalUniqueId: {
      uri: '/ups/resources/$(tenantId)conference/?globalUniqueID=$(globalUniqueId)',
      method: 'GET',
      headers: {
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        globalUniqueId: { key: '$(globalUniqueId)', flags: 's' },
      },
      response: {
        conferences: { key: 'conferences', flags: '' }
      },
    },
    searchOccurence: {
      uri: '/ups/resources/$(tenantId)conference/?conferenceId=$(conferenceId)&originalStartTime=$(originalStartTime)',
      method: 'GET',
      headers: {
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' },
        originalStartTime: { key: '$(originalStartTime)', flags: 's' },
      },
      response: {
        conferences: { key: 'conferences', flags: '' }
      },
    },
    schedule: {
      uri: '/ups/resources/$(tenantId)conference/',
      method: 'POST',
      headers: {
        'content-type': 'application/vnd.avaya.portal.meeting.schedule.v2+json',
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        number: { key: '$(number)', flags: 's' },
        parentMeetingNumber: { key: '$(parentMeetingNumber)', flags: 's' },
        subject: { key: '$(subject)', flags: 's' },
        startTime: { key: '$(startTime)', flags: 's' },
        duration: { key: '$(duration)', flags: 's' },
        timeZoneId: { key: '$(timeZoneId)', flags: 's' },
        accessPIN: { key: '$(accessPIN)', flags: 'A' },
        terminationCondition: { key: '$(terminationCondition)', flags: 's' },
        moderatorPIN: { key: '$(moderatorPIN)', flags: 'A' },
        daily: { key: '$(daily)', flags: '' },
        weekly: { key: '$(weekly)', flags: '' },
        monthly: { key: '$(monthly)', flags: '' },
        yearly: { key: '$(yearly)', flags: '' },
        recurrenceEnd: { key: '$(recurrenceEnd)', flags: '' },
        client: { key: '$(client)', flags: 's' },
        globalUniqueId: { key: '$(globalUniqueId)', flags: 's' },
      },
      body: {
        conference: [ {
            number: '$(number)',
            parentMeetingNumber: '$(parentMeetingNumber)',
            subject: '$(subject)',
            startTime: '$(startTime)',
            duration: '$(duration)',
            timeZoneId: '$(timeZoneId)',
            accessPIN: '$(accessPIN)',
            moderatorPIN: '$(moderatorPIN)',
            advancedProperties: {
              terminationCondition: '$(terminationCondition)'
            },
            daily : '$(daily)',
            weekly : '$(weekly)',
            monthly : '$(monthly)',
            yearly : '$(yearly)',
            recurrenceEnd : '$(recurrenceEnd)',
            client: '$(client)',
            globalUniqueID: '$(globalUniqueId)'
          }
        ]
      },
      response: {
        meetingId: { key: 'number', flags: 's' },
        conferenceId: { key: 'conferenceId', flags: 's' }
      }
    },
    reschedule: {
      uri: '/ups/resources/$(tenantId)conference/$(conferenceId)',
      method: 'PUT',
      headers: {
        'content-type': 'application/vnd.avaya.portal.meeting.update.v2+json',
        'authorization': '$(token)'
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' },
        conference: { key: '$(conference)', flags: '' }
      },
      body: {
        conference: '$(conference)'
      },
      response: {
        success: { key: 'success', flags: 'b' }
      }
    },
    delete: {
      uri: '/ups/resources/$(tenantId)conference/$(conferenceId)',
      method: 'DELETE',
      headers: {
        'content-type': 'application/vnd.avaya.portal.meeting.cancel.v1+json',
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' }
      },
      body: {
      },
      response: {
      }
    },
    deleteOccurrenceByStartTime: {
      uri: '/ups/resources/$(tenantId)conference/$(conferenceId)?startTime=$(startTime)',
      method: 'DELETE',
      headers: {
        'content-type': 'application/vnd.avaya.portal.meeting.cancel.v1+json',
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' },
        startTime: { key: '$(startTime)', flags: 's' }
      },
      body: {
      },
      response: {
      }
    },
    deleteOccurrence: {
      uri: '/ups/resources/$(tenantId)conference/$(conferenceId)?&originalStartTime=$(originalStartTime)',
      method: 'DELETE',
      headers: {
        'content-type': 'application/vnd.avaya.portal.meeting.cancel.v1+json',
        'authorization': '$(token)',
      },
      args: {
        tenantId: { key: '$(tenantId)', flags: 's', format: 'tenants/$0/', default: 'default' },
        token: { key: '$(token)', flags: 'S', format: 'UPToken $0' },
        conferenceId: { key: '$(conferenceId)', flags: 's' },
        originalStartTime: { key: '$(originalStartTime)', flags: 's' }
      },
      body: {
      },
      response: {
      }
    }
  };

  function RoomsAPI(serverBase) {
    this.$super(serverBase);
  }

  Object.getOwnPropertyNames(methods).forEach(function (name) {
    RoomsAPI.prototype[name] = function(args) {
      return this._exec(methods[name], args);
    };
  }, this);

  module.exports = API.extend(RoomsAPI);
});



